interface IState {
  confirm_modal_title: string;
  confirm_modal_message: string;
  confirm_modal_open: boolean;
  action_after_confirm: () => void;
  invalid_fields: any[];
  loading_state: boolean;
  block_title?: string;
  show_side_modal_block?: string;
  side_modal_block_mode?: string;
  code_info?: any;
  code_list: any;
  code_data_all: any;
  code_data: any;
  selected_date_filter: any;
  selected_start_date: any;
  selected_end_date: any;
  global_filter: string;
  default_filters: any;
  default_sorting: any;
  default_clicked: string;
  isModalOpen: boolean;
  modalTitle: string;
  modalMessage: string;
  totalCount: any;
  typing: boolean;
  typingTimeout: any;
  page: number;
  limit: number;
  isPageSizeModal: boolean;
  clickedCodeFromList: number;
  init_expiry_date: Date | null;
  no_data_message: 'Searching for CPT codes' | 'No available data' | '';
  cpt_categories: string[];
  cpt_category_range: string[];
  disableSaveButton: boolean;
  clickSaveButton: boolean;
  category_dropdown_choices: any[];
}

interface IProps {
  classes: any
}


export class CPTSection extends React.Component<
  IProps,
  IState,
  any
  > {
  constructor(props) {
    super(props);

    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'code',
          title: 'CPT Code',
        },
        {
          name: 'description',
          title: 'Description',
        },
      ],
      rows: [],
    };

    this.state = {
      category_dropdown_choices: [],
      cpt_categories: [],
      cpt_category_range: [],
      no_data_message: '',
      init_expiry_date: null,
      confirm_modal_title: '',
      confirm_modal_message: '',
      confirm_modal_open: false,
      action_after_confirm: () => { },
      invalid_fields: [],
      clickedCodeFromList: -1,
      loading_state: true,
      code_list: [],
      code_info: undefined,
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_data: table,
      code_data_all: table,
      block_title: '',
      selected_date_filter: {
        id: '',
        value: 'This Month',
        label: 'This Month',
      },
      selected_start_date: new Date(),
      selected_end_date: new Date(),
      global_filter: '',
      default_filters: [],
      default_sorting: [{
        columnName : 'code',
        direction : 'asc'
      }],
      default_clicked: '',
      isModalOpen: false,
      isPageSizeModal: false,
      modalTitle: '',
      modalMessage: '',
      totalCount: 0,
      typing: false,
      typingTimeout: 0,
      page: 0,
      limit: 10,
      disableSaveButton: false,
      clickSaveButton: false,
    };
  }


  componentDidMount() {
    this.fetchCPTCategories()
    .then(()=>this.initialData())
    .catch(()=>this.initialData())
    document.addEventListener('keydown', this.escFunction, false);
    
  }

  escFunction = (event) => {
    if (event.keyCode === 27) {
      this.handleCloseSideModalBlock()
    }
  }


  initialData = () => {
    this.setState({ no_data_message: 'Searching for CPT codes' })
    searchCPT(
      10,
      0,
      this.state.default_filters,
      this.state.global_filter,
      this.state.default_sorting,
      this.state.default_clicked).then(response => {
      this.setState({ no_data_message: 'No available data' })
      if (response && response.error === undefined) {
        const table = this.generateDataForTable(
          response.result ? response.result : response,
        );
        this.setState({
          code_list: response,
          code_data_all: table,
          code_data: table,
          loading_state: false,
          totalCount:
            response.total && response.total.count ? response.total.count : 50,
          page: 0,
          limit: 10,
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        const table = this.generateDataForTable([]);
        this.setState({
          code_list: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to view CPT codes.',
          page: 0,
        });
      } else {
        const table = this.generateDataForTable([]);
        this.setState({
          code_list: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Loading failed',
          modalMessage: 'An error occured while loading the list of codes.',
          page: 0,
        });
      }
    });
  };


















  /* CRUD FUNCTIONS */

  addCPTIndexDB = (data : any[]) =>{
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try{
        helper.saveToStore(db, 'cpt_list', data)
        .then(res => console.log('RES', res))
        .catch(e => console.log(e))
      }catch(e){
        console.log(e)
      }finally{}
    })
  }

  patchCPTIndexDB = (data : any) =>{
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try{
        helper.updateEntryByKey(db,'cpt_list',data['_id'],data)
        .then(res => console.log('RES', res))
        .catch(e => console.log(e))
      }catch (e){
        console.log(e)
      }finally{}
    })
  }

  fetchCPTCategories = async () => {
    this.setState({ loading_state : true})
    await Utils.StorageService('cpt_list')
      .then(cpts => {
        if (Array.isArray(cpts) && cpts.length > 0) {
          let category_arr: string[] = []
          let category_ranges: string[] = []
          let category_dropdown_choices_arr : any[] = []
          cpts.filter(item => {
            if (!category_arr.includes(item.category)) {
              category_arr.push(item.category)
              // const category_range = item.code.match(/[a-z]+|\d+/ig)[0] + '0000' + '-' + item.code.match(/[a-z]+|\d+/ig)[0] + '9999'
              category_ranges.push(item.code)
              category_dropdown_choices_arr.push({name: item.category, value: item.category})
              return true;
            }
            return false;
          })
          this.setState({
            cpt_categories: category_arr,
            cpt_category_range: category_ranges,
            category_dropdown_choices : category_dropdown_choices_arr
          }, () => this.setState({ loading_state : false}))
        }
      })
      .catch(err => console.log(err));
  }

  patchCPT = (data: any) => {
    // let payload = data
    data['gender'] = data['gender_limited'] ? data['gender'] : '';

    if (data['expiry_date'] === '') {
      if (this.state.init_expiry_date !== null && this.state.init_expiry_date !== undefined) {
        data['expiry_date'] = this.state.init_expiry_date
      } else {
        delete data['expiry_date'];
      }

    }
    if (data['status'] !== undefined) {
      delete data['status']
    }

    this.setState({
      loading_state: true,
      no_data_message: 'Searching for CPT codes'
    })
    editCPT(JSON.stringify(data), data['_id']).then(response => {
      this.setState({
        no_data_message: 'No available data',
        loading_state: false
      })
      if (response && response.error === undefined && response.message) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.patchCPTIndexDB(data)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have ${response.message} code!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to edit CPT codes.'
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while saving!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }

  addCPTCodes = (data: any) => {
    let payload: any[] = [];

    if (data['expiry_date'] === '') {
      delete data['expiry_date'];
    }
    if (data['status'] !== undefined) {
      delete data['status']
    }
    let add_code = data
    add_code['gender'] = add_code['gender_limited'] ? add_code['gender'] : '';
    add_code['date_updated'] = moment(new Date()).format(moment.defaultFormat)
    if (!Array.isArray(data)) {
      payload.push(add_code)
    }
    this.setState({
      loading_state: true,
      no_data_message: 'Searching for CPT codes'
    })

    addCPTCodes(JSON.stringify(payload)).then(response => {
      this.setState({
        loading_state: false,
        no_data_message: 'No available data'
      })
      if (response && response.error === undefined && response.message && response.result) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.addCPTIndexDB(response.result)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have successfully added ${response.result.length} CPT code/s!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to add CPT codes.'
        });
      } else if (response && response.error && response.error.statusCode === 400 && response.error.message){
        this.setState({
          isModalOpen: true,
          modalTitle: 'Duplicate Codes',
          modalMessage: response.error.message
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while adding!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while adding!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }


  deleteCPT = (id: string) => {
    if (!id) {
      return
    }
    this.setState({ loading_state: true })
    deleteCPT(id).then(response => {
      this.setState({
        loading_state: false
      })
      this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
      if (response && response.error) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while deleting the CPT Code!'
        });
      }
      else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: 'You have successfully deleted a CPT code!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while deleting the ICD10 Code!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }














  /* TABLE AND PAGE FUNCTIONS */
  generateDataForTable(response: any) {
    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'code',
          title: 'CPT Code',
        },
        {
          name: 'description',
          title: 'Description',
        },
      ],
      rows: [],
    };
    for (const cnt in response) {
      const item = response[cnt];
      const temp_row: any = {};
      temp_row['code'] = item['code'];
      temp_row['description'] = item['description'];
      table['rows'].push(temp_row);
    }

    return table;
  }

  changePageUpdate = (limit: number, skip: number) => {
    this.handleCloseSideModalBlock();
    this.setState({ no_data_message: 'Searching for CPT codes' })
    this.setState({
      loading_state: true,
    });
    if (
      this.state.default_filters.length !== 0 ||
      this.state.global_filter !== '' ||
      this.state.default_sorting.length !== 0 ||
      this.state.default_clicked !== ''
    ) {
      searchCPT(
        limit,
        skip,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        this.state.default_clicked,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view CPT codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    } else {
      this.setState({ no_data_message: 'Searching for CPT codes' })
      getCPT(limit, skip).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view CPT codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleChangeSorting = (sorting: any) => {
    this.setState({ no_data_message: 'Searching for CPT codes' })
    this.setState({
      loading_state: true,
    });
    if (sorting && sorting.length > 0) {

      searchCPT(
        this.state.limit,
        this.state.page * this.state.limit,
        this.state.default_filters,
        this.state.global_filter,
        sorting,
        this.state.default_clicked,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            default_sorting: sorting,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view CPT codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleFilterByColumn = (filter: any) => {
    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
      default_filters: filter,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          no_data_message: 'Searching for CPT codes',
          loading_state: true,
        });
        if (self.state.default_filters !== 0) {
          searchCPT(
            self.state.limit,
            0,
            filter,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          ).then(response => {
            self.setState({ no_data_message: 'No available data' })
            if (response && response.error === undefined) {
              const table = self.generateDataForTable(
                response.result ? response.result : response,
              );
              self.setState({
                code_list: response,
                code_data_all: table,
                code_data: table,
                loading_state: false,
                totalCount:
                  response.total && response.total.count
                    ? response.total.count
                    : 50,
              });
            } else if (response && response.error && response.error.statusCode === 401) {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Unauthorized',
                modalMessage:
                  'You are not authorized to view CPT codes.',
              });
            } else {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Loading failed',
                modalMessage:
                  'An error occured while loading the list of codes.',
              });
            }
          });
        } else {
          self.initialData();
        }
      }, 2000),
    });

  };

  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }
    self.setState({
      global_filter: value,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          no_data_message: 'Searching for CPT codes',
          loading_state: true,
          limit: 10,
        });
        if (self.state.global_filter !== '') {
          searchCPT(
            self.state.limit,
            0,
            self.state.default_filters,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          ).then(response => {
            self.setState({ no_data_message: 'No available data' })

            if (response && response.error === undefined) {
              const table = self.generateDataForTable(
                response.result ? response.result : response,
              );
              self.setState({
                code_list: response,
                code_data_all: table,
                code_data: table,
                loading_state: false,
                totalCount:
                  response.total && response.total.count
                    ? response.total.count
                    : 50,
                limit: 10,
              });
            } else if (response && response.error && response.error.statusCode === 401) {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Unauthorized',
                modalMessage:
                  'You are not authorized to view CPT codes.',
              });
            } else {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Loading failed',
                modalMessage:
                  'An error occured while loading the list of codes.',
              });
            }
          });
        } else {
          self.initialData();
        }
      }, 2000),
    });
  };

  handleClickRow = (row: any) => {
    const { code_list } = this.state;

    let data = {};
    let status = 'Active';
    let gender_limited = false;

    if (code_list && code_list.result) {
      if( code_list.result[row]['effectivity_date'] &&
          moment(code_list.result[row]['effectivity_date']).isValid() &&
          moment(code_list.result[row]['effectivity_date']).diff(new Date()) > 0){
            status = 'Awaiting Activation'
      }
      if (
        code_list.result[row]['expiry_date'] &&
        moment(code_list.result[row]['expiry_date']).isValid() &&
        moment(code_list.result[row]['expiry_date']).diff(new Date()) <= 0
      ) {
        status = 'Deactivated';
      }
      gender_limited = code_list.result[row]['gender'] &&
        code_list.result[row]['gender'] !== '' ?
        true :
        false;
      const gender = code_list.result[row]['gender'] ? code_list.result[row]['gender'] : '';
      if (code_list.result[row]['expiry_date'] && moment(code_list.result[row]['expiry_date']).isValid()) {
        this.setState({
          init_expiry_date: code_list.result[row]['expiry_date']
        })
      } else {
        this.setState({
          init_expiry_date: null
        })
      }
      data = {
        _id: code_list.result[row]['_id'],
        code: code_list.result[row]['code'],
        description: code_list.result[row]['description'],
        category: code_list.result[row]['category'],
        gender_limited: gender_limited,
        gender: gender,
        effectivity_date: code_list.result[row]['effectivity_date'],
        expiry_date: code_list.result[row]['expiry_date'],
        status: status,
        date_updated: code_list.result[row]['date_updated'],

      }
    }

    this.handleConfirmationSubmit(data,true)
    this.setState(
      {
        code_info: data,
        block_title: data['code'] ? data['code'] : '',
      },
      () => {
        this.handleChangeSideModalBlockMode('view');
      },
    );
  };


  handlePageChange = (currentPage: number, skip: number) => {
    this.setState({
      page: currentPage,
    });

    this.changePageUpdate(this.state.limit, skip);
  };

  handlePageSizeChange = value => {
    let pageLimit = 100000;

    if (this.state.totalCount >= pageLimit) {
      this.setState({
        isPageSizeModal: true,
      });
    } else {
      this.setState({
        limit: value,
      });

      this.changePageUpdate(value, value * this.state.page);
    }
  };

  handleClickItemFromList = (key: string) => {
    this.handleCloseSideModalBlock()
    this.setState({
      no_data_message: 'Searching for CPT codes',
      page: 0,
      loading_state: true,
    });
    if (key !== undefined) {
      searchCPT(
        this.state.limit,
        0,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        key,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            default_clicked: key,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view CPT codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleSetClickedCodeFromList = (item: number, key: string) => {
    this.setState({
      clickedCodeFromList: item,
    });
    this.handleClickItemFromList(key);
  }


















  /* ACTIONS / SIDE MODAL FUNCTIONS */

  handleCloseSideModalBlock = () => {
    this.setState({
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_info: {},
    });
  };

  handleChangeSideModalBlockMode = (value: string) => {
    let title = '';
    if (value && value === 'edit' && this.state.block_title) {
      title = 'Edit ' + this.state.block_title;
      let data = cloneDeep(this.state.code_info)
      const reform_data = {
        _id : data._id,
        code: data.code,
        description: data.description,
        category: data.category,
        effectivity_date: data.effectivity_date,
        expiry_date: data.expiry_date,
        status: data.status,
        gender_limited: Boolean(data.gender_limited),
        gender: data.gender_limited ? data.gender : '',
      }
      this.setState({ code_info: reform_data })
    } else if (value && value === 'view' && this.state.block_title) {
      title = this.state.block_title;
    } else if (value && value === 'add') {
      title = 'Add CPT Code';
    }
    this.setState(
      {
        side_modal_block_mode: value,
        block_title: title,
      },
      () => {
        this.handleOpenBlock();
      },
    );
  };

  handleOpenBlock = () => {
    this.setState({
      show_side_modal_block: 'block',
    }, () => this.passDataToSideModalBlockBody());
  };

  handleInvalidFieldsPrompt = () => {
    this.setState({
      isModalOpen: true,
      modalTitle: 'Incomplete/Invalid Fields',
      modalMessage: 'Please complete the form before proceeding.',
    })
  }


  passDataToSideModalBlockBody = () => {
    // if (this.state.code_info.expiry_date === '' ||
    //   this.state.code_info.expiry_date === undefined ||
    //   this.state.code_info.expiry_date === null) {
    //   this.state.code_info.expiry_date = new Date()
    // }
    // if (this.state.code_info.effectivity_date === '' ||
    //   this.state.code_info.effectivity_date === undefined ||
    //   this.state.code_info.effectivity_date === null) {
    //   this.state.code_info.effectivity_date = new Date()

    // }
    if (!this.state.code_info.gender_limited) {
      this.state.code_info.gender = ''
    }

    this.setState({
      code_info: this.state.code_info
    });
  };


  handleConfirmationSubmit = (formFields?: any, disableButton?: boolean) => {
    this.setState({ loading_state: true })

    if (formFields) {
      let invalid_fields = this.getInvalidFields(formFields, disableButton);
      if (!invalid_fields || invalid_fields.length === 0) {
        if(this.state.disableSaveButton || !this.state.clickSaveButton){
          return
        }
        if (this.state.side_modal_block_mode === 'edit') {
          this.setState({
            clickSaveButton: false,
            confirm_modal_message: 'Are you sure you want to edit this CPT code?',
            confirm_modal_open: true,
            confirm_modal_title: 'Confirm edit CPT code',
            action_after_confirm: () => this.patchCPT(formFields)
          })
        } else if (this.state.side_modal_block_mode === 'add') {
          this.setState({
            clickSaveButton: false,
            confirm_modal_message: 'Are you sure you want to add this CPT code?',
            confirm_modal_open: true,
            confirm_modal_title: 'Confirm add CPT code',
            action_after_confirm: () => this.addCPTCodes(formFields)
          })
        }
      } else {
        this.handleInvalidFieldsPrompt()
      }
    }
  }

  getInvalidFields = (formFields: any, disableButton?: boolean) => {
    let invalid_fields_submit: any[] = [];
    let invalid_fields_editing: any[] = [];
    let exclude_fields = {}
    if (excludeFields !== undefined) {
      exclude_fields = excludeFields
    }
    Object.keys(formFields).forEach(field => {
      if (checkInvalidFields !== undefined &&
        checkInvalidFields.includes(field) &&
        !Object.keys(exclude_fields).includes(field) &&
        (formFields[field] === '' ||
          formFields[field] === undefined ||
          formFields[field] === null ||
          ((field === 'effectivity_date' ||
            field === 'expiry_date') &&
            !moment(formFields[field]).isValid()))) {
              if(!disableButton){
                invalid_fields_submit.push(field)
              }else{
                invalid_fields_editing.push(field)
              }

      }
    })
    if(formFields && formFields['gender_limited'] !== undefined && formFields['gender_limited'] == true && !formFields['gender']){
      if(!disableButton){
        invalid_fields_submit.push('gender')
      }else{
        invalid_fields_editing.push('gender')
      }
    }
    if(!invalid_fields_editing || invalid_fields_editing.length === 0){
      this.setState({disableSaveButton : false})
    }else if(!this.state.disableSaveButton){
      this.setState({disableSaveButton : true})
    }
    this.setState({ loading_state: false, invalid_fields: invalid_fields_submit })
    return invalid_fields_submit
  }




  render(): JSX.Element {
    const { classes } = this.props;

    let codes : any = [];
    if(this.state.cpt_categories !== undefined && this.state.cpt_category_range !== undefined){
      codes = this.state.cpt_categories && this.state.cpt_category_range
      ? this.state.cpt_categories.map(key => (
        <ListItemText
          key={key}
          classes={{
            root: classes.codeListItemsRoot,
            primary:
              this.state.clickedCodeFromList ===
                this.state.cpt_categories.indexOf(key)
                ? classes.codeListItemsSelected
                : classes.codeListItems,
          }}
          onClick={() => {
            if (
              this.state.cpt_categories.indexOf(key) !==
              this.state.clickedCodeFromList
            ) {
              this.setState({
                clickedCodeFromList: this.state.cpt_categories.indexOf(key),
              });
              this.handleClickItemFromList(
                key
                );
            } else {
              this.setState({
                clickedCodeFromList: -1,
              });
              this.handleClickItemFromList('');
            }
          }}
        >
          <br />
          {/* {this.state.cpt_categories.indexOf(key) + 1}  */}
          {
            // this.state.cpt_category_range &&
            // this.state.cpt_categories.indexOf(key) &&
            // this.state.cpt_category_range[this.state.cpt_categories.indexOf(key)] &&
            // this.state.cpt_category_range[this.state.cpt_categories.indexOf(key)].match(/[a-z]+|\d+/ig) ?
          this.state.cpt_category_range[this.state.cpt_categories.indexOf(key)].slice(0,4)+'0000-'
          + this.state.cpt_category_range[this.state.cpt_categories.indexOf(key)].slice(0,4)+'9999'  
          + ' : '}<br />
          {key}

        </ListItemText>
      ))
      : [];
    }
    

    return (
      <div>
        {this.state.loading_state ? <Loader /> : null}
        <SideModalBlock
          blockPosition={'fixed'}
          title={this.state.block_title}
          editIconDescription={'Edit Code'}
          showBlock={this.state.show_side_modal_block}
          classes={classes}
          handleClose={this.handleCloseSideModalBlock}
          sideModalBlockMode={this.state.side_modal_block_mode}
          changeMode={this.handleChangeSideModalBlockMode}
        >
          {this.state.side_modal_block_mode ? (
            <SideModalBody
              formFields={this.state.code_info}
              invalidFields={this.state.invalid_fields}
              classes={this.props.classes}
              handleClose={this.handleCloseSideModalBlock}
              // deleteIcon={'Delete Code'}
              disableSaveButton={this.state.disableSaveButton}
              dynamicDropdownChoices={this.state.category_dropdown_choices}
              refreshData={(data: any) => {
                this.setState({ code_info: data },
                  () => this.handleConfirmationSubmit(data, true))
              }}
              delete={(data: any) => {
                if (this.state.side_modal_block_mode) {
                  if (this.state.side_modal_block_mode === 'edit') {
                    this.setState({
                      confirm_modal_message: 'Are you sure you want to delete this CPT code?',
                      confirm_modal_open: true,
                      confirm_modal_title: 'Confirm delete CPT code',
                      action_after_confirm: () => this.deleteCPT(data['_id'])
                    })
                  }
                }
              }}
              handleSave={(formFields?: any) => {
                if (this.state.side_modal_block_mode) {
                  this.setState({clickSaveButton : true}, ()=>this.handleConfirmationSubmit(formFields))
                }
              }}
              formName={'CPT'}
              mode={this.state.side_modal_block_mode ? this.state.side_modal_block_mode : ''}
              checkBoxWithDropDownFieldsInitState={this.state.code_info && this.state.code_info['gender_limited'] ? true : false}
            />
          ) : null}

        </SideModalBlock>

        <Grid container>


          <Grid container item xs={12} md={3}>
            <Grid
              container
              className={classes.scrollList}
              style={{ maxHeight: 715, overflow: 'auto' }}
            >
              {this.state.cpt_categories && this.state.cpt_category_range ? (
                <List>
                  <li key={'version'} className={classes.codeHeader}>
                    VERSION
                </li>
                  <li
                    key={'year-of-release-value'}
                    className={classes.codeHeaderValue}
                  >
                    2020
                </li>
                  <ListItemText key={'effectivity-date'}>
                    <div className={classes.codeHeader}>EFFECTIVITY DATE</div>
                    <div className={classes.codeHeaderValue}>
                      Jan 1, 2020
                  </div>
                  </ListItemText>
                  {codes}
                  {/* <NestedList classes={classes} listValues={list_values} clickedItemFromList={this.state.clickedCodeFromList} handleSetClickedCodeFromList={this.handleSetClickedCodeFromList} /> */}
                </List>
              ) : null}

            </Grid>
          </Grid>

          <Grid container item xs={12} md={9} className={classes.selectedpage}>
            <Grid container>
              <Grid item xs={12} md={9}>
                <TextField
                  style={{ width: '100%' }}
                  id="code_search"
                  placeholder="Search a code or description"
                  label=""
                  margin="none"
                  fullWidth
                  className={classes.searchField}
                  variant="outlined"
                  value={this.state.global_filter}
                  inputProps={{ 'aria-label': 'bare' }}
                  onChange={this.handleSearch}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>

              <Grid item xs={8} md={3} className={classes.searchBar}>
                <FontAwesomeIcon
                  icon={faPlus}
                  className={classes.addFieldIcon}
                />
                <Link className={classes.addACode} onClick={
                  () => {
                    this.setState(
                      {
                        block_title: 'Add CPT Code',
                      },
                      () => {
                        const data = {
                          code: '',
                          description: '',
                          category: '',
                          effectivity_date: '',
                          expiry_date: '',
                          status: '',
                          gender_limited: false,
                          gender: '',
                        }
                        this.setState({
                          code_info: data,
                          disableSaveButton: true
                        }, () => this.handleChangeSideModalBlockMode('add'))
                      },
                    );
                  }}>Add a Code</Link>
              </Grid>
              <GridHorizontalDivider height="30px" />
              <Grid style={{ minHeight: 690, overflow: 'auto' }}>
                <TableComponent
                  id="tab-all"
                  totalCount={this.state.totalCount}
                  update={this.changePageUpdate}
                  updateSorting={this.handleChangeSorting}
                  rows={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['rows']
                      : []
                  }
                  columns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columns']
                      : []
                  }
                  message={
                    this.state.global_filter
                      ? 'No available data.'
                      : this.state.no_data_message
                  }
                  onClickRow={this.handleClickRow}
                  disableSelect
                  disableSearch
                  formattedColumns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['formattedColumns']
                      : {}
                  }
                  columnExtensions={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columnExtensions']
                      : []
                  }
                  onFilterChange={this.handleFilterByColumn}
                  onSortingChange={this.handleChangeSorting}
                  onPageChange={this.handlePageChange}
                  ongPageSizeChange={this.handlePageSizeChange}
                  defaultFilter={this.state.default_filters}
                  currentPage={this.state.page}
                  limit={this.state.limit}
                  pageSizes={[10, 25, 50, 100]}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <MsgModal
          onClose={() => this.setState({ isModalOpen: false })}
          title={this.state.modalTitle}
          isModalOpen={this.state.isModalOpen}
          message={this.state.modalMessage}
        />
        <ConfirmationModal
          onClose={() => this.setState({ confirm_modal_open: false })}
          title={this.state.confirm_modal_title}
          isModalOpen={this.state.confirm_modal_open}
          submitType={'confirm'}
          onSubmit={() => {
            if (this.state.action_after_confirm) {
              this.state.action_after_confirm()
            }
          }}
          message={this.state.confirm_modal_message} />
      </div>
    );
  }
}
