import Fetch, { requestOption } from '../index'

const fetch = new Fetch();
fetch.getAccessToken();

export const getICD = (limit: number, skip: number) => {

    let filters = {
        limit: limit,
        skip: skip,
      };
    let option: requestOption = {
        method: 'GET'
    }
    if(fetch.getAccessToken()){
        return fetch.call(option, `icd10-codes/search?filter=${JSON.stringify(filters)}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `icd10-codes/search?filter=${JSON.stringify(filters)}`, true))
}

export const searchICD = (limit: number, offset: number, filters: any, query: string, sorting?: any, key?: string,) => {

    // console.log('KEY', key)

    let filter_key_str = '';
    if (key !== undefined && key !== '') {
      filter_key_str = `&filter[where][main_category]=${key}`;
    }
    let sorting_str = '';
    if (sorting && sorting.length !== 0) {
      sorting_str =
        '&filter[order]=' +
        sorting[0]['columnName'] +
        ' ' +
        sorting[0]['direction'];
    }
    let filter_str = '';
    if (filters) {
      filters.forEach(filter => {
        filter_str =
          filter_str + '&' + filter['columnName'] + '=' + filter['value'];
      });
    }
    let search = query ? '&search=' + query : '';
    let ICD_URL =
      `icd10-codes/search?filter[limit]=${limit}&filter[skip]=${offset}` +
      search +
      filter_str +
      sorting_str +
      filter_key_str;
    if (limit === 0)
        ICD_URL =
        `icd10-codes/search?filter[skip]=${offset}` +
        search +
        filter_str +
        sorting_str +
        filter_key_str;
    let option: requestOption = {
        method: 'GET'
    }
 
    if(fetch.getAccessToken()){
        return fetch.call(option, ICD_URL, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, ICD_URL, true))
}

export const addICD10Codes = (payload: any) => {

    let option: requestOption = {
        method: 'POST',
        body: payload
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `icd10-codes/multiple`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `icd10-codes/multiple`, true))
}

export const editICD = (payload: any, id: string) => {

    let option: requestOption = {
        method: 'PUT',
        body: payload
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `icd10-codes/edit/${id}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `icd10-codes/edit/${id}`, true))
}

export const deleteICD = (id: string) => {

    let option: requestOption = {
        method: 'DELETE'
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `icd10-codes/${id}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `icd10-codes/${id}`, true))
}