import * as React from 'react';
import { Link, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useState, useEffect } from 'react';
import classNames from 'classnames';

const useStyles = makeStyles({
	sideBarLists: {
		padding: 0,
		margin: 0,
        listStyle: 'none',
        position: 'sticky',
        top: 20
	},
	sideBarLink: {
		color: "#272e4c",
		margin: "0 0 10px",
		display: "block"
    },
    LinkActive: {
        color: "#2C2E8E",
        fontWeight: "bold",
        textDecoration: "underline"
    }
});

export interface ISideMenuProps {
    items: any,
    activeItems: (item) => void,
    reset?: any,
}

export const SideMenu : React.FC<ISideMenuProps> = (props: ISideMenuProps): JSX.Element => {
    const {items , activeItems } = props;
    const [activeClass, setActiveClass] = useState(0);
    const classes = useStyles();

    useEffect(() => {
        // resets the active item to zero when
        // the reset props changes
        if(props.reset !== undefined){
            setActiveClass(0)
            activeItems(items[0].name)
        }
    }, [props.reset])

    const sideBarlinkClick = (itemName, index) => {
        activeItems(itemName)
        setActiveClass(index)
    }

    return (
        <>
            <ul className={classes.sideBarLists}>
                {
                    items.map((item, i) => (
                        <li>
                            {
                                item.label !== undefined &&
                                <Typography variant={'subtitle2'} style={{color:'#929292', fontSize: '12px'}}>{ item.label }</Typography>
                            }
                            <Link 
                                className={classNames(classes.sideBarLink, activeClass === i ? classes.LinkActive : null)} 
                                onClick={() => sideBarlinkClick(item.name, i)}
                            >
                                { item.name } 
                            </Link>
                        </li>
                    ))
                }
            </ul>
        </>
    )
}