interface IState {
  confirm_modal_title: string;
  confirm_modal_message: string;
  confirm_modal_open: boolean;
  action_after_confirm: () => void;
  invalid_fields: any[];
  loading_state: boolean;
  block_title?: string;
  show_side_modal_block?: string;
  side_modal_block_mode?: string;
  code_info?: any;
  code_list: any;
  code_data_all: any;
  code_data: any;
  selected_date_filter: any;
  selected_start_date: any;
  selected_end_date: any;
  global_filter: string;
  default_filters: any;
  default_sorting: any;
  default_clicked: string;
  isModalOpen: boolean;
  modalTitle: string;
  modalMessage: string;
  action_after_info: () => void;
  totalCount: any;
  typing: boolean;
  typingTimeout: any;
  page: number;
  limit: number;
  isPageSizeModal: boolean;
  clickedCodeFromList: number;
  init_expiry_date: Date | null;
  icd_categories: string[];
  no_data_message: 'Searching for ICD10 codes' | 'No available data' | '';
  category_dropdown_choices: any[];
  disableSaveButton: boolean;
  clickSaveButton: boolean;
}

interface IProps {
  classes: any
}

export class ICD10Section extends React.Component<
  IProps,
  IState,
  any
  > {
  constructor(props) {
    super(props);

    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'full_code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'full_description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'full_code',
          title: 'ICD10 Code',
        },
        {
          name: 'full_description',
          title: 'Description',
        },
      ],
      rows: [],
    };

    this.state = {
      category_dropdown_choices: [],
      icd_categories: [],
      no_data_message: '',
      init_expiry_date: null,
      confirm_modal_title: '',
      confirm_modal_message: '',
      confirm_modal_open: false,
      action_after_confirm: () => { },
      invalid_fields: [],
      clickedCodeFromList: -1,
      loading_state: true,
      code_list: [],
      code_info: undefined,
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_data: table,
      code_data_all: table,
      block_title: '',
      selected_date_filter: {
        id: '',
        value: 'This Month',
        label: 'This Month',
      },
      selected_start_date: new Date(),
      selected_end_date: new Date(),
      global_filter: '',
      default_filters: [],
      default_sorting: [
        {
        columnName : 'full_code',
        direction : 'asc'
      }
    ],
      default_clicked: '',
      isModalOpen: false,
      isPageSizeModal: false,
      modalTitle: '',
      modalMessage: '',
      action_after_info: () => { },
      totalCount: 0,
      typing: false,
      typingTimeout: 0,
      page: 0,
      limit: 10,
      disableSaveButton: false,
      clickSaveButton: false,
    };
  }

  componentDidMount() {
    this.fetchICDCategories()
      .then(() => this.initialData())
      .catch(() => this.initialData())

    document.addEventListener('keydown', this.escFunction, false);
  }

  escFunction = (event) => {
    if (event.keyCode === 27) {
      this.handleCloseSideModalBlock()
    }
  }

  initialData = () => {
    this.setState({ no_data_message: 'Searching for ICD10 codes' })
    searchICD(
      10,
      0,
      this.state.default_filters,
      this.state.global_filter,
      this.state.default_sorting,
      this.state.default_clicked)
    .then(response => {
      this.setState({ no_data_message: 'No available data' })
      // console.log('RESP', response)
      if (response && response.error === undefined) {
        const table = this.generateDataForTable(
          response.result ? response.result : response,
        );
        this.setState({
          code_list: response,
          code_data_all: table,
          code_data: table,
          loading_state: false,
          totalCount:
            response.total && response.total.count ? response.total.count : 50,
          page: 0,
          limit: 10,
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        const table = this.generateDataForTable([]);
        this.setState({
          code_list: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to view ICD10 codes.',
          page: 0,
        });
      }
      else {
        const table = this.generateDataForTable([]);
        this.setState({
          code_list: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Loading failed',
          modalMessage: 'An error occured while loading the list of codess.',
          page: 0,
        });
      }
    }).catch(() => {
      const table = this.generateDataForTable([]);
      this.setState({
        code_list: [],
        code_data_all: table,
        loading_state: false,
        isModalOpen: true,
        modalTitle: 'Loading failed',
        modalMessage: 'An error occured while loading the list of codesa.',
        page: 0,
      });
    });
  };






















  /* CRUD FUNCTIONS */

  addICD10IndexDB = (data : any[]) =>{
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try{
        helper.saveToStore(db, 'icd10_list', data)
        .then(res => console.log('RES', res))
        .catch(e => console.log(e))
      }catch(e){
        console.log(e)
      }finally{}
    })
  }

  patchICDIndexDB = (data : any) =>{
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try{
        helper.updateEntryByKey(db,'icd10_list',data['_id'],data)
        .then(res => console.log('RES', res))
        .catch(e => console.log(e))
      }catch (e){
        console.log(e)
      }finally{}
    })
  }

  fetchICDCategories = async () => {

    await Utils.StorageService('icd10_list')
      .then(icds => {
        if (Array.isArray(icds) && icds.length > 0) {
          let category_arr: string[] = []
          let category_dropdown_choices_arr : any[] = []
          icds.filter(item => {
            if (item && item.main_category && !category_arr.includes(item.main_category)) {
              category_arr.push(item.main_category)
              category_dropdown_choices_arr.push({name: item.main_category, value: item.main_category})
              return true;
            }
            return false;
          })
          this.setState({
            icd_categories: category_arr,
            category_dropdown_choices : category_dropdown_choices_arr
          })
        }
      })
      .catch(err => console.log(err));
  }

  patchICD = (data: any) => {

    // console.log('DATA', data['expiry_date'])
    if (data['expiry_date'] === '') {
      if (this.state.init_expiry_date !== null && this.state.init_expiry_date !== undefined) {
        data['expiry_date'] = this.state.init_expiry_date
      } else {
        delete data['expiry_date'];
      }
    }
    if (data['status'] !== undefined) {
      delete data['status']
    }

    this.setState({
      loading_state: true,
      no_data_message: 'Searching for ICD10 codes'
    })
    // console.log('STRINGIFIED', JSON.stringify(data))
    editICD(JSON.stringify(data), data['_id']).then(response => {
      this.setState({
        loading_state: false,
        no_data_message: 'No available data'
      })
      if (response && response.error === undefined && response.message !== undefined) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.patchICDIndexDB(data)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have ${response.message} code!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to edit ICD10 codes.'
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while saving!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }



  addICDCodes = (data: any) => {
    let payload: any[] = [];
    data['date_updated'] = moment(new Date()).format(moment.defaultFormat)
    if (data['expiry_date'] === '') {
      delete data['expiry_date'];
    }
    if (data['status'] !== undefined) {
      delete data['status']
    }

    if (!Array.isArray(data)) {
      payload.push(data)
    }
    this.setState({
      loading_state: true
    })



    addICD10Codes(JSON.stringify(payload)).then(response => {
      this.setState({
        loading_state: false
      })
      if (response && response.error === undefined && response.message && response.result) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.addICD10IndexDB(response.result)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have successfully added ${response.result.length} ICD10 code/s!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to add ICD10 codes.'
        });
      } else if (response && response.error && response.error.statusCode === 400 && response.error.message){
        this.setState({
          isModalOpen: true,
          modalTitle: 'Duplicate Codes',
          modalMessage: response.error.message
        });
      } 
      else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while adding!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while adding!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }

  deleteICD = (id: string) => {
    if (!id) {
      return
    }
    this.setState({ loading_state: true })
    deleteICD(id).then(response => {
      this.setState({
        loading_state: false
      })
      this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
      if (response && response.error && response.error.statusCode && response.error.statusCode !== 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while deleting ICD10 Code!'
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to delete ICD10 codes.'
        });
      }
      else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: 'You have successfully deleted an ICD10 code!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while deleting ICD10 Code!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }










  /* TABLE AND PAGE FUNCTIONS */

  generateDataForTable(response: any) {
    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'full_code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'full_description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'full_code',
          title: 'ICD10 Code',
        },
        {
          name: 'full_description',
          title: 'Description',
        },
      ],
      rows: [],
    };
    for (const cnt in response) {
      const item = response[cnt];
      const temp_row: any = {};
      temp_row['full_code'] = item['full_code'];
      temp_row['full_description'] = item['full_description'];
      table['rows'].push(temp_row);
    }

    return table;
  }

  changePageUpdate = (limit: number, skip: number) => {
    this.handleCloseSideModalBlock();
    this.setState({
      loading_state: true,
      no_data_message: 'Searching for ICD10 codes'
    });
    if (
      this.state.default_filters.length !== 0 ||
      this.state.global_filter !== '' ||
      this.state.default_sorting.length !== 0 ||
      this.state.default_clicked !== ''
    ) {
      searchICD(
        limit,
        skip,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        this.state.default_clicked,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view ICD10 codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    } else {
      this.setState({ no_data_message: 'Searching for ICD10 codes' })
      getICD(limit, skip).then(response => {
        this.setState({ no_data_message: 'No available data' })
        // console.log('RESP', response)
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view ICD10 codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleChangeSorting = (sorting: any) => {
    this.setState({
      loading_state: true,
      no_data_message: 'Searching for ICD10 codes'
    });
    if (sorting && sorting.length > 0) {
      searchICD(
        this.state.limit,
        this.state.page * this.state.limit,
        this.state.default_filters,
        this.state.global_filter,
        sorting,
        this.state.default_clicked,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            default_sorting: sorting,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view ICD10 codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleFilterByColumn = (filter: any) => {
    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
      default_filters: filter,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          loading_state: true,
          no_data_message: 'Searching for ICD10 codes'
        });
        if (self.state.default_filters !== 0) {
          searchICD(
            self.state.limit,
            0,
            filter,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          ).then(response => {
            self.setState({ no_data_message: 'No available data' })
            if (response && response.error === undefined) {
              const table = self.generateDataForTable(
                response.result ? response.result : response,
              );
              self.setState({
                code_list: response,
                code_data_all: table,
                code_data: table,
                loading_state: false,
                totalCount:
                  response.total && response.total.count
                    ? response.total.count
                    : 50,
              });
            } else if (response && response.error && response.error.statusCode === 401) {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Unauthorized',
                modalMessage: 'You are not authorized to view ICD10 codes.',
              });
            } else {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Loading failed',
                modalMessage:
                  'An error occured while loading the list of codes.',
              });
            }
          });
        } else {
          self.initialData();
        }
      }, 2000),
    });
  };

  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }
    self.setState({
      global_filter: value,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          loading_state: true,
          limit: 10,
          no_data_message: 'Searching for ICD10 codes'
        });
        if (self.state.global_filter !== '') {
          searchICD(
            self.state.limit,
            0,
            self.state.default_filters,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          ).then(response => {
            self.setState({ no_data_message: 'No available data' })
            if (response && response.error === undefined) {
              const table = self.generateDataForTable(
                response.result ? response.result : response,
              );
              self.setState({
                code_list: response,
                code_data_all: table,
                code_data: table,
                loading_state: false,
                totalCount:
                  response.total && response.total.count
                    ? response.total.count
                    : 50,
                limit: 10,
              });
            } else if (response && response.error && response.error.statusCode === 401) {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Unauthorized',
                modalMessage: 'You are not authorized to view ICD10 codes.',
              });
            } else {
              const table = self.generateDataForTable([]);
              self.setState({
                code_list: [],
                code_data_all: table,
                loading_state: false,
                isModalOpen: true,
                modalTitle: 'Loading failed',
                modalMessage:
                  'An error occured while loading the list of codes.',
              });
            }
          });
        } else {
          self.initialData();
        }
      }, 2000),
    });
  };

  handleClickRow = (row: any) => {
    const { code_list } = this.state;

    let data = {};
    let status = 'Active';

    if (code_list && code_list.result) {
      if( code_list.result[row]['effectivity_date'] &&
          moment(code_list.result[row]['effectivity_date']).isValid() &&
          moment(code_list.result[row]['effectivity_date']).startOf('day').diff(moment(new Date()).startOf('day')) > 0){
            status = 'Awaiting Activation'
      }
      if (
        code_list.result[row]['expiry_date'] &&
        moment(code_list.result[row]['expiry_date']).isValid() &&
        moment(code_list.result[row]['expiry_date']).diff(moment(new Date()).startOf('day')) <= 0
      ) {
        status = 'Deactivated';
      }
      if (code_list.result[row]['expiry_date'] && moment(code_list.result[row]['expiry_date']).isValid()) {
        this.setState({
          init_expiry_date: code_list.result[row]['expiry_date']
        })
      } else {
        this.setState({
          init_expiry_date: null
        })
      }
      data = {
        _id: code_list.result[row]['_id'],
        category_code: code_list.result[row]['category_code'],
        diagnosis_code: code_list.result[row]['diagnosis_code'] ? code_list.result[row]['diagnosis_code'].toString() : undefined,
        full_code: code_list.result[row]['full_code'],
        abbreviated_description: code_list.result[row]['abbreviated_description'],
        full_description: code_list.result[row]['full_description'],
        category_title: code_list.result[row]['category_title'],
        main_category: code_list.result[row]['main_category'],
        effectivity_date: code_list.result[row]['effectivity_date'],
        expiry_date: code_list.result[row]['expiry_date'],
        status: status,
        date_updated: code_list.result[row]['date_updated'],
      }
    }

    this.handleConfirmationSubmit(data,true)
    this.setState(
      {
        code_info: data,
        block_title: data['full_code'] ? data['full_code'] : '',
      },
      () => {
        this.handleChangeSideModalBlockMode('view');
      },
    );
  };

  handlePageChange = (currentPage: number, skip: number) => {
    this.setState({
      page: currentPage,
    });

    this.changePageUpdate(this.state.limit, skip);
  };

  handlePageSizeChange = value => {
    let pageLimit = 100000;

    if (this.state.totalCount >= pageLimit) {
      this.setState({
        isPageSizeModal: true,
      });
    } else {
      this.setState({
        limit: value,
      });

      this.changePageUpdate(value, value * this.state.page);
    }
  };

  handleClickItemFromList = (key: string) => {
    this.handleCloseSideModalBlock()
    this.setState({
      page: 0,
      loading_state: true,
      no_data_message: 'Searching for ICD10 codes'
    });
    if (key !== undefined) {
      searchICD(
        this.state.limit,
        0,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        key,
      ).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : response,
          );
          this.setState({
            code_list: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            default_clicked: key,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 50,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view ICD10 codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_list: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };










  /* ACTIONS / SIDE MODAL FUNCTIONS */

  handleCloseSideModalBlock = () => {
    this.setState({
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_info: {},
    });
  };


  handleChangeSideModalBlockMode = (value: string) => {
    let title = '';
    if (value && value === 'edit' && this.state.block_title) {
      title = 'Edit ' + this.state.block_title;
    } else if (value && value === 'view' && this.state.block_title) {
      title = this.state.block_title;
    } else if (value && value === 'add') {
      title = 'Add ICD-10 Code';
    }
    this.setState(
      {
        side_modal_block_mode: value,
        block_title: title,
      },
      () => {
        this.handleOpenBlock();
      },
    );
  };

  handleOpenBlock = () => {
    this.setState({
      show_side_modal_block: 'block',
    }, () => this.passDataToSideModalBlockBody());
  };

  handleInvalidFieldsPrompt = () => {
    this.setState({
      isModalOpen: true,
      modalTitle: 'Incomplete/Invalid Fields',
      modalMessage: 'Please complete the form before proceeding.',
    })
  }

  passDataToSideModalBlockBody = () => {
    // if (this.state.code_info.expiry_date === '' ||
    //   this.state.code_info.expiry_date === undefined ||
    //   this.state.code_info.expiry_date === null) {
    //   this.state.code_info.expiry_date = new Date()
    // }
    // if (this.state.code_info.effectivity_date === '' ||
    //   this.state.code_info.effectivity_date === undefined ||
    //   this.state.code_info.effectivity_date === null) {
    //   this.state.code_info.effectivity_date = new Date()

    // }

    this.setState({
      code_info: this.state.code_info
    });
  };



  handleConfirmationSubmit = (formFields?: any, disableButton?: boolean) => {
    this.setState({ loading_state: true })

    if (formFields) {
      let invalid_fields = this.getInvalidFields(formFields, disableButton);
      if (!invalid_fields || invalid_fields.length === 0) {
        if(this.state.disableSaveButton || !this.state.clickSaveButton){
          return
        }
        if (this.state.side_modal_block_mode === 'edit') {
          this.setState({
            clickSaveButton: false,
            confirm_modal_message: 'Are you sure you want to edit this ICD10 code?',
            confirm_modal_open: true,
            confirm_modal_title: 'Confirm edit ICD10 code',
            action_after_confirm: () => this.patchICD(formFields)
          })
        } else if (this.state.side_modal_block_mode === 'add') {
          this.setState({
            clickSaveButton: false,
            confirm_modal_message: 'Are you sure you want to add this ICD10 code?',
            confirm_modal_open: true,
            confirm_modal_title: 'Confirm add ICD10 code',
            action_after_confirm: () => this.addICDCodes(formFields)
          })
        }
      } else {
        this.handleInvalidFieldsPrompt()
      }
    }
  }

  getInvalidFields = (formFields: any, disableButton?: boolean) => {
    let invalid_fields_submit: any[] = [];
    let invalid_fields_editing: any[] = [];
    let exclude_fields = {}
    if (excludeFields !== undefined) {
      exclude_fields = excludeFields
    }
    Object.keys(formFields).forEach(field => {
      if (checkInvalidFields !== undefined &&
        checkInvalidFields.includes(field) &&
        !Object.keys(exclude_fields).includes(field) &&
        (formFields[field] === '' ||
          formFields[field] === undefined ||
          formFields[field] === null)) {
            if(!disableButton){
              invalid_fields_submit.push(field)
            }else{
              invalid_fields_editing.push(field)
            }

      }
    })
    if(!invalid_fields_editing || invalid_fields_editing.length === 0){
      this.setState({disableSaveButton : false})
    }else if(!this.state.disableSaveButton){
      this.setState({disableSaveButton : true})
    }
    this.setState({ loading_state: false, invalid_fields: invalid_fields_submit })
    return invalid_fields_submit
  }



  render(): JSX.Element {
    const { classes } = this.props;

    let codes = this.state.icd_categories
      ? this.state.icd_categories.map(key => (
        <ListItemText
          key={key}
          classes={{
            root: classes.codeListItemsRoot,
            primary:
              this.state.clickedCodeFromList ===
                this.state.icd_categories.indexOf(key)
                ? classes.codeListItemsSelected
                : classes.codeListItems,
          }}
          onClick={() => {
            if (
              this.state.icd_categories.indexOf(key) !==
              this.state.clickedCodeFromList
            ) {
              this.setState({
                clickedCodeFromList: this.state.icd_categories.indexOf(key),
              });
              this.handleClickItemFromList(key);
            } else {
              this.setState({
                clickedCodeFromList: -1,
              });
              this.handleClickItemFromList('');
            }
          }}
        >
          <br />
          {this.state.icd_categories.indexOf(key) + 1} {key}
        </ListItemText>
      ))
      : [];


    return (
      <div>
        {this.state.loading_state ? <Loader /> : null}
        <SideModalBlock
          blockPosition={'fixed'}
          title={this.state.block_title}
          editIconDescription={'Edit Code'}
          showBlock={this.state.show_side_modal_block}
          classes={classes}
          handleClose={this.handleCloseSideModalBlock}
          sideModalBlockMode={this.state.side_modal_block_mode}
          changeMode={this.handleChangeSideModalBlockMode}

        >
          {this.state.side_modal_block_mode ? (
            <SideModalBody
              formFields={this.state.code_info}
              invalidFields={this.state.invalid_fields}
              classes={this.props.classes}
              handleClose={this.handleCloseSideModalBlock}
              refreshData={(data: any) => {
                this.setState({ code_info: data },
                  () => this.handleConfirmationSubmit(data, true))
              }}
              delete={(data: any) => {
                if (this.state.side_modal_block_mode) {
                  if (this.state.side_modal_block_mode === 'edit') {
                    this.setState({
                      confirm_modal_message: 'Are you sure you want to delete this ICD10 code?',
                      confirm_modal_open: true,
                      confirm_modal_title: 'Confirm delete ICD10 code',
                      action_after_confirm: () => this.deleteICD(data['_id'])
                    })
                  }
                }
              }}
              handleSave={(formFields?: any) => {
                if (this.state.side_modal_block_mode) {
                  this.setState({clickSaveButton : true}, ()=>this.handleConfirmationSubmit(formFields))
                }
              }}
              // deactivateIcon={'Deactivate'}
              formName={'ICD10'}
              mode={this.state.side_modal_block_mode ? this.state.side_modal_block_mode : ''}
              dynamicDropdownChoices={this.state.category_dropdown_choices}
              disableSaveButton={this.state.disableSaveButton}
            />
          ) : null}
        </SideModalBlock>

        <Grid container>
          <Grid container item xs={12} md={3}>
            <Grid
              container
              className={classes.scrollList}
              style={{ maxHeight: 715, overflow: 'auto' }}
            >
              <List>
                <li key={'year-of-release'} className={classes.codeHeader}>
                  YEAR OF RELEASE
                </li>
                <li
                  key={'year-of-release-value'}
                  className={classes.codeHeaderValue}
                >
                  2020
                </li>
                <ListItemText key={'effectivity-date'}>
                  <div className={classes.codeHeader}>EFFECTIVITY DATE</div>
                  <div className={classes.codeHeaderValue}>
                    Jan 1, 2020
                  </div>
                </ListItemText>
                {codes}
              </List>
            </Grid>
          </Grid>

          <Grid container item xs={12} md={9} className={classes.selectedpage}>
            <Grid container>
              <Grid item xs={12} md={9}>
                <TextField
                  style={{ width: '100%' }}
                  id="code_search"
                  placeholder="Search a code or description"
                  label=""
                  margin="none"
                  fullWidth
                  className={classes.searchField}
                  variant="outlined"
                  value={this.state.global_filter}
                  inputProps={{ 'aria-label': 'bare' }}
                  onChange={this.handleSearch}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>

              <Grid item xs={8} md={3} className={classes.searchBar}>
                <FontAwesomeIcon
                  icon={faPlus}
                  className={classes.addFieldIcon}
                />
                <Link className={classes.addACode} onClick={
                  () => {
                    this.setState(
                      {
                        block_title: 'Add ICD-10 Code',
                      },
                      () => {
                        const data = {
                          full_code: '',
                          full_description: '',
                          category_title: '',
                          main_category: '',
                          effectivity_date: '',
                          expiry_date: '',
                        }
                        this.setState({
                          code_info: data,
                          disableSaveButton: true
                        }, () => this.handleChangeSideModalBlockMode('add'))
                      },
                    );
                  }}>Add a Code</Link>
              </Grid>
              <GridHorizontalDivider height="30px" />
              <Grid style={{ minHeight: 690, overflow: 'auto' }}>
                <TableComponent
                  id="tab-all"
                  totalCount={this.state.totalCount}
                  update={this.changePageUpdate}
                  updateSorting={this.handleChangeSorting}
                  rows={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['rows']
                      : []
                  }
                  columns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columns']
                      : []
                  }
                  message={
                    this.state.global_filter
                      ? 'No available data'
                      : this.state.no_data_message
                  }
                  onClickRow={this.handleClickRow}
                  disableSelect
                  disableSearch
                  formattedColumns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['formattedColumns']
                      : {}
                  }
                  columnExtensions={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columnExtensions']
                      : []
                  }
                  onFilterChange={this.handleFilterByColumn}
                  onSortingChange={this.handleChangeSorting}
                  onPageChange={this.handlePageChange}
                  ongPageSizeChange={this.handlePageSizeChange}
                  defaultFilter={this.state.default_filters}
                  currentPage={this.state.page}
                  limit={this.state.limit}
                  pageSizes={[10, 25, 50, 100]}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <MsgModal
          onClose={() => {
            this.setState({
              isModalOpen: false,
            })
          }}
          title={this.state.modalTitle}
          isModalOpen={this.state.isModalOpen}
          message={this.state.modalMessage}
        />
        <ConfirmationModal
          onClose={() => this.setState({ confirm_modal_open: false })}
          title={this.state.confirm_modal_title}
          isModalOpen={this.state.confirm_modal_open}
          submitType={'confirm'}
          onSubmit={() => {
            if (this.state.action_after_confirm) {
              this.state.action_after_confirm()
            }
          }}
          message={this.state.confirm_modal_message} />
      </div>
    );
  }
}
